@tailwind base;
@tailwind components;
@tailwind utilities;
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: #003e29; */
  background-color: #181a18;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.footer-styles {
  background-color: #181a18;
  display: flex;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 60px;
  justify-content: center;
  align-items: center;
  margin: 100em, 0, 0, 0;
}

.fashion-line {
  background-color: #fa9f42;
  margin-top: 5em;
  width: 100%;
  height: 0.5em;
}

/* footer styles */
.footer-icon {
  font-size: 16pt;
}

.footer-gh {
  color: #fa9f42;
}
.footer-box {
  padding: 0.5em;
  display: flex;
}

/* about section */

.photo-text {
  color: white;
}

.photo-box {
  position: relative;
}

.background-image {
  margin-left: 4em;
}

.name-intro {
  position: absolute;
  top: 5em;
  left: 1.5em;
  font-size: 50pt;
  font-weight: 600;
  text-shadow: 1px 1px 5px black;
}

.name-style {
  position: absolute;
  font-size: 60pt;
  font-weight: 900;
  top: 5.25em;
  left: 1.5em;
  text-shadow: 2px 2px 8px black;
  color: #fa9f42;
}

.position-info {
  position: absolute;
  text-shadow: 2px 2px 8px black;
  top: 4.5em;
  font-size: 50pt;
  left: 13em;
  font-weight: 700;
  text-align: center;
  padding: 0.5em;
  background-color: #fa9f42;
}

.about-box {
  display: flex;
  color: white;
  background-color: #292929;
  margin-left: 7em;
  margin-right: 7em;
  position: absolute;
  top: 48em;
  padding: 5em;
}

.headshot {
  padding: 1em;
  margin-bottom: 4em;
  width: 20em;
}

.bio-text {
  font-size: 26pt;
  padding-left: 1em;
}

.change-bio-text {
  color: #fa9f42;
  font-size: 28pt;
  font-weight: bold;
}
/* end about section */

/* nav styles */

.nav-bar {
  display: flex;
  justify-content: space-between;
  padding: 1em;
}

.nav-elements-box {
  display: flex;
  color: white;
  font-size: 16pt;
  font-weight: 200;
  margin-top: 1em;
  margin-right: 1.5em;
}

.initials {
  background: white;
  border: #fa9f42 3px solid;
  padding: 0.5em;
  border-radius: 50%;
  font-size: 20pt;
  margin-left: 1em;
  font-weight: 900;
  color: #181a18;
}

.hover-nav:hover {
  color: #fa9f42;
}

.contact-me {
  cursor: pointer;
}

/* portfolio styles */
.icon-position {
  display: flex;
  justify-content: center;
  margin-top: 0.5em;
  margin-bottom: 6em;
}
.icon-prof-styles {
  z-index: 50;
}

.hb-carousel {
  position: relative;
}

.hike-buddy-description {
  display: none;
  margin-top: -20em;
  color: white;
  font-size: 25pt;
  text-align: center;
  z-index: 50;
}

.hb-span-text {
  font-weight: 700;
  color: #fa9f42;
}

.hike-buddy-img:hover {
  opacity: 0.2;
}

.hike-buddy-img:hover ~ .hike-buddy-description {
  display: block;
}

/* git blogging styles */
.gb-carousel {
  position: relative;
}

.git-blogging-description {
  display: none;
  margin-top: -18em;
  color: white;
  font-size: 25pt;
  text-align: center;
  z-index: 50;
}

.gb-span-text {
  font-weight: 700;
  color: #fa9f42;
}

.blog-img:hover {
  opacity: 0.2;
}

.blog-img:hover ~ .git-blogging-description {
  display: block;
}

/* parkscape styles */
.parkscape-description {
  display: none;
  margin-top: -18em;
  font-size: 25pt;
  color: white;
  text-align: center;
}

.parkscape-img:hover {
  opacity: 0.2;
}

.parkscape-img:hover ~ .parkscape-description {
  display: block;
}

.ps-span-text {
  font-weight: 700;
  color: #fa9f42;
}

/* weather dash styles */
.weather-dash-description {
  display: none;
  margin-top: -18em;
  font-size: 25pt;
  color: white;
  text-align: center;
}

.weather-dash-img:hover {
  opacity: 0.2;
}

.weather-dash-img:hover ~ .weather-dash-description {
  display: block;
}

.wd-span-text {
  font-weight: 700;
  color: #fa9f42;
}

/* team generator */
.roster-description {
  display: none;
  margin-top: -18em;
  font-size: 25pt;
  color: white;
  text-align: center;
}

.roster-img:hover {
  opacity: 0.2;
}

.roster-img:hover ~ .roster-description {
  display: block;
}

.or-span-text {
  font-weight: 700;
  color: #fa9f42;
}

/* password gen */
.password-description {
  display: none;
  margin-top: -18em;
  font-size: 25pt;
  color: white;
  text-align: center;
}

.password-gen-img:hover {
  opacity: 0.2;
}

.password-gen-img:hover ~ .password-description {
  display: block;
}

.pg-span-text {
  color: #fa9f42;
  font-weight: 700;
}

.github-btn {
  background-color: #fa9f42;
  margin-top: 0.5em;
  color: white;
  border-radius: 10%;
  padding: 0.2em;
  justify-content: center;
}

.btn-link {
  display: flex;
  justify-content: center;
}

.modal-background {
  z-index: 3;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 175%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact-modal {
  background-color: #292929;
  z-index: 4;
  width: 50%;
  height: 21em;
  margin-top: -40em;
}

.modal-contact-header {
  background-color: #fa9f42;
  color: white;
  font-weight: 900;
  font-size: 20pt;
  padding: 0.5em;
}

.contact-icon-box {
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact-icon {
  padding: 3em;
}

.icon-styles {
  font-size: 4em;
  color: #fa9f42;
}

.contact-message {
  text-align: center;
  font-size: 14pt;
  color: white;
  margin: 1em 5em 0 5em;
}

.fashion-line-2 {
  background-color: #fa9f42;
  width: 100%;
  height: 0.25em;
}

/* start resume page */
.resume {
  color: white;
}

.smile-headshot {
  width: 40em;
  height: 20em;
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  border: #292929 solid 8px;
  margin-left: 12em;
  margin-bottom: 1em;
}

.profile-box {
  display: flex;
}

.text-bio-box {
  display: flex;
  flex-direction: column;
}

.second-bio-text {
  margin-left: 13em;
  font-weight: 500;
  margin-right: 15em;
}

.bio-name-text {
  font-size: 30pt;
  color: #fa9f42;
  font-weight: 700;
  margin-left: 5.2em;
}

.bio-position-text {
  font-size: 12pt;
  margin-left: 13em;
  margin-top: 2em;
}

.cv-btn {
  background-color: #292929;
  border-radius: 5px;
  padding: 0.5em;
  margin-left: 13em;
  margin-right: 3em;
  margin-top: 1em;
}

.resume-btn {
  background-color: #292929;
  border-radius: 5px;
  padding: 0.5em;
}

.cv-btn:hover {
  border: #fa9f42 solid 2px;
  color: #fa9f42;
}

.resume-btn:hover {
  border: #fa9f42 solid 2px;
  color: #fa9f42;
}

/* start what i do section of resume */
.what-i-do {
  color: white;
  font-weight: 700;
  border-top: #fa9f42 solid 3px;
  font-size: 20pt;
  margin-bottom: 1em;
  margin-top: 1em;
  padding-left: 0.5em;
  padding-top: 1em;
}

.what-i-do-box {
  display: flex;
  flex-wrap: wrap;
}

.skill-title-box {
  display: flex;
}

.skill-description-box {
  width: 18em;
}

.skill-box {
  margin-left: 3.5em;
  margin-bottom: 2em;
}

.icon-resume {
  font-size: 20pt;
  margin-left: -1.5em;
  margin-right: 0.5em;
}

.skill-title {
  font-size: 16pt;
  font-weight: 500;
}

/* .bottom-row {
  margin-bottom: 5em;
} */

.selected {
  color: #fa9f42;
}

/* start testimonials */

.box-of-referals {
  margin-bottom: 10rem;
  display: flex;
  flex-wrap: wrap;
}

.reference-box {
  background-color: #292929;
  width: 30em;
  margin-left: 6em;
  padding: 2em;
  border-radius: 10px;
  margin-top: 4em;
}

.reference-text-box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.testimonials-title {
  font-size: 20pt;
  padding-left: 0.5em;
  padding-top: 1em;
  color: white;
  font-weight: 700;
}

.reference-img {
  width: 100px;
  height: 100px;
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  position: absolute;
  margin-top: -3em;
  left: 4em;
}

.reference-img:hover {
  width: 105px;
  height: 105px;
}

.reference-text {
  margin-left: 3em;
  margin-bottom: 1em;
  font-style: italic;
  opacity: 0.7;
}

.top-quote-w {
  position: absolute;
  font-size: 50pt;
  font-style: italic;
  left: 0.8em;
  margin-top: -1em;
  color: #fa9f42;
}

.bottom-quote-w {
  position: absolute;
  font-size: 50pt;
  font-style: italic;
  left: 8.3em;
  margin-top: 1.55em;
  color: #fa9f42;
}

.referer-postion {
  color: #fa9f42;
}

.reference-img-2 {
  width: 100px;
  height: 100px;
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  position: absolute;
  margin-top: -3em;
  margin-left: -3.8em;
}

.reference-img-2:hover {
  width: 105px;
  height: 105px;
}

.top-quote-d {
  position: absolute;
  font-size: 50pt;
  font-style: italic;
  margin-top: -1em;
  color: #fa9f42;
  left: 9.5em;
}

.bottom-quote-d {
  position: absolute;
  font-size: 50pt;
  font-style: italic;
  margin-top: 1.2em;
  color: #fa9f42;
  left: 17em;
}

